import React from 'react';
import Card from "./components/Card"
import './App.css';
import images from "./images.json";
class App extends React.Component {
  state = {
    ids: [1,2,3,4,5,6,7,8,9],
    clicked: [""],
    score: 0,
    topScore: 0
  };
  shuffleId = ()=> {
    const ids = this.state.ids;
    for (var i = ids.length; i > 0; i--) {
      var j = Math.floor(Math.random() * ids.length);
      var t = ids[i - 1];
      ids[i - 1] = ids[j];
      ids[j] = t;
  }
  return ids;
  }
  componentDidMount(){
    this.setState({ids : this.shuffleId()});
  }
  endGame = ()=>{
    alert("you lost");
    this.setState({clicked : [],score : 0});

  }
  continueGmae=(id)=>{
    let top = this.state.topScore;
    let current = this.state.score;
    if(top !==9){
      //top can incre
      if(current === top){
        this.setState({topScore : top+1});
      }
    }
    if(current<=9){
      this.setState({score: (current+1)%9, clicked : this.state.clicked.concat(id)});
    }
    if(current ===8){
      alert("you won");
      this.setState({score : 9, clicked : []});
    }
  }
  handleClick = e=>{
    // console.log("in click");
    // console.log(this.state.clicked);
    
    const id = Number(e.target.id);
    if (this.state.clicked.indexOf(id) === -1) {
      this.continueGmae(id);
      // console.log(this.state.clicked);
      
    }else{
      this.endGame();
    }
    this.setState({ids : this.shuffleId()})
  }


  render() {
    return (
      <div>
        <h1>
          Clicky Game !
        </h1>
        <h2>
        Score : {this.state.score}
        </h2>
        <h2>
        Top Score : {this.state.topScore}
        </h2>
        <h2>
        Clicked : {this.state.clicked}
        </h2>
        {this.state.ids.map(item=> <Card id={item} image={images[item-1].image} handleClick={this.handleClick}/>)}
      </div>
    );
  }
}

export default App;
