import React from "react";
import "./style.css";

function Card(props) {
  return (
    <div className="card">
      <div className="img-container" onClick={props.handleClick}>
        <img id={props.id} src={props.image} alt={props.id} />
      </div>
      <span>
          {props.id}
      </span>
    </div>
  );
}

export default Card;